class CguError extends Error {
    constructor(reason) {
        super('TOS Error');
        this.reason = reason;
        this.message = 'TOS Error ' + reason + '- Cannot access app: ' + this.getErrorMessage();
    }
    static getTextReason(reason) {
        switch (reason) {
            case FsCguStatus.NEED_SIGNING_UPDATE:
                return 'NEED_SIGNING_UPDATE';
            case FsCguStatus.UP_TO_DATE:
                return 'UP_TO_DATE';
            case FsCguStatus.NEVER_SIGNED:
                return 'NEVER_SIGNED';
            case FsCguStatus.NOT_APPLICABLE:
                return 'NOT_APPLICABLE';
            case FsCguStatus.NOT_LOGGED_IN:
                return 'NOT_LOGGED_IN';
        }
    }
    getErrorMessage() {
        switch (this.reason) {
            case FsCguStatus.NEED_SIGNING_UPDATE:
                return 'The most recent version of the TOS are not signed';
            case FsCguStatus.UP_TO_DATE:
                return 'Latest TOS accepted. This should not be an error';
            case FsCguStatus.NEVER_SIGNED:
                return 'The TOS are not signed';
            case FsCguStatus.NOT_APPLICABLE:
                return 'The TOS are not applicable for this type of profile';
            case FsCguStatus.NOT_LOGGED_IN:
                return 'The user is not logged in so it cannot accept the TOS';
        }
    }
}

